function sliderMain() {
    var swiper = new Swiper('.main .swiper', {
        slidesPerView: 1,
        effect: 'fade',
        fadeEffect: {
            crossFade: true
        },
        navigation: {
            nextEl: '.main .swiper-button-next',
            prevEl: '.main .swiper-button-prev',
        }
    })
}

function sliderAdvantages() {
    var swiper = new Swiper('.advantages .swiper', {
        slidesPerView: 4,
        spaceBetween: 15,
        navigation: {
            nextEl: '.advantages .swiper-button-next',
            prevEl: '.advantages .swiper-button-prev',
        },
        breakpoints: {
            320: {
                slidesPerView: 1,
            },
            400: {
                slidesPerView: 2,
            },
            451: {
                slidesPerView: 3,
            }
        }
    })
}

function sliderRooms() {
    var swiper = new Swiper('.rooms .swiper', {
        slidesPerView: 3,
        spaceBetween: 100,
        centeredSlides: true,
        loop: true,
        navigation: {
            nextEl: '.rooms .swiper-button-next',
            prevEl: '.rooms .swiper-button-prev',
        },
        breakpoints: {
            320: {
                slidesPerView: 1,
                spaceBetween: 15,
                centeredSlides: false,
            },
            451: {
                slidesPerView: 2,
                spaceBetween: 55,
                centeredSlides: false,
            },
            577: {
                slidesPerView: 3,
                spaceBetween: 35,
            },
            769: {
                slidesPerView: 3,
                spaceBetween: 50,
            },
            993: {
                slidesPerView: 3,
                spaceBetween: 100,
            }
        }
    })
}

