function gallery() {
    var swiper = new Swiper(".gallery .mySwiper", {
        slidesPerView: 3,
        spaceBetween: 20,
        freeMode: true,
        watchSlidesProgress: true,
        direction: "horizontal",
    });
    var swiper2 = new Swiper(".gallery .mySwiper2", {
        spaceBetween: 10,
        slidesPerView: 1,
        thumbs: {
            swiper: swiper,
        },
        navigation: {
            nextEl: ".swiper-button-next",
            prevEl: ".swiper-button-prev",
        },
    });
}


$(document).ready(function() {

    sliderMain();
    sliderAdvantages();
    sliderRooms();
    sliderServices();
    sliderAdvantage2();
    sliderStaff();
    sliderOffers();
    gallery();


    // Hover-Click
    $(".header__item").hover(onIn, onOut);
    $(".header__item .header__item-show").click(function() {
        if ($(this).parents(".header__item").hasClass("header__item--active")) {
            $(this).parents(".header__item").removeClass("header__item--active")
        } else {
            $(this).parents(".header__item").addClass("header__item--active")
        }
    })

    let ItemShow = $('.header__item-show')
    let Item = $('.header__item')

    $(document).mouseup(function (e) {
        if ( !ItemShow.is(e.target) && ItemShow.has(e.target).length === 0 &&
            !Item.is(e.target) && Item.has(e.target).length === 0

        ) {
            Item.removeClass('header__item--active')
        }
    })

    // Burger - Menu
    $('.header__burger').click(function () {
        $(this).toggleClass('header__burger--active')
        $('.header__center').toggleClass('header__center--active')
        $('body').toggleClass('fixed-body')
        $('.sidebar').toggleClass('sidebar--active')
    })

    // Accordion
    $(".sidebar__item-show").click((function() {
        if($(this).parent(".sidebar__item").hasClass("sidebar__item--active")){
            $(".sidebar__item").removeClass("sidebar__item--active")
        } else{
            $(".sidebar__item").removeClass("sidebar__item--active")
            $(this).parent(".sidebar__item").toggleClass("sidebar__item--active")
        }
    }));

    $('.main__slider').click(function () {
        $('.sidebar__item').removeClass('sidebar__item--active')
    })

    $(document).mouseup( function(e) {
        var div = $( ".sidebar__item" );
        if ( !div.is(e.target)
            && div.has(e.target).length === 0 ) {
            if ($(div).hasClass("sidebar__item--active")){
                $(div).removeClass("sidebar__item--active")
            }
        }
    });

    // Up
    var btn = $('.up');
    $(window).scroll(function() {
        if ($(window).scrollTop() > 300) {
            btn.addClass('up--active');
        } else {
            btn.removeClass('up--active');
        }
    });
    btn.on('click', function(e) {
        e.preventDefault();
        $('html, body').animate({scrollTop:0}, '300');
    });

    // Tabs
    $(".tab").click(function () {
        let path = $(this).attr("data-tab-path")
        $(this).siblings(".tab").removeClass("tab--active")
        $(this).addClass("tab--active")

        $(this).parents(".tabs").find(".tabs__content").removeClass("tabs__content--active")
        $(this).parents(".tabs").find(`.tabs__content[data-content-path="${path}"]`).addClass("tabs__content--active")
    })

})

function onIn() {
    if (window.innerWidth > 768) {
        let el = $(this)
        setTimeout(function() {
            if (el.is(':hover')) {
                console.log(el)
                el.addClass("header__item--active")

            }
        }, 200);
    }
}

function onOut() {
    if (window.innerWidth > 768) {
        $(this).removeClass("header__item--active")
    }
}